module.exports = "<Navbar>\n  <Navbar.Header>\n    <Navbar.Brand>\n      <a href=\"#home\">Brand</a>\n    </Navbar.Brand>\n    <Navbar.Toggle />\n  </Navbar.Header>\n  <Navbar.Collapse>\n    <Navbar.Form pullLeft>\n      <FormGroup>\n        <FormControl type=\"text\" placeholder=\"Search\" />\n      </FormGroup>{' '}\n      <Button type=\"submit\">Submit</Button>\n    </Navbar.Form>\n  </Navbar.Collapse>\n</Navbar>;\n"


